
body{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: rgb(0, 0, 0);
}

.App {
  align-items: center;
  background-color: rgb(14, 14, 114);
  display: flex;
  flex-direction: column;
  padding: 40px;
  width: 500px;
}


.buttonBox{
  border: 1px rgb(188, 188, 188) solid;
  border-radius: 5px;
  height: 40px;
  width: 40px;
  color: rgb(244, 244, 244);
  background-color: rgb(186, 171, 171);
  margin: 3px;
  justify-content: center;
}

.buttonBox:hover {
  background-color: rgb(27, 27, 79);
}


h3 {
  background-color: rgb(50, 49, 49);
  width: 150px;
  align-items: center;
  margin: 20px;
  padding-right: 83px;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
}


.buttonContainer{
  display: flex;
  flex-wrap: wrap;
  width: 45%;
  align-items: center;
  justify-content: center;
}